class RestoDetailLike extends HTMLElement {
  constructor() {
    super();
  }

  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML = `
      <button aria-label="like this restaurant" id="likeButton" class="like">
        <icon-like></icon-like>
      </button>
    `;
  }
}

customElements.define('resto-detail-like', RestoDetailLike);
