class ReviewForm extends HTMLElement {
  constructor() {
    super();
  }

  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML = `
      <section class="form-add-review" id="form-add">
        <h2 tabindex="0">Tulis Ulasan Anda</h2>
        <form id="formReview">
          <div class="input">
            <label for="inputName">Nama</label>
            <input name="inputName" id="inputName" type="text"/>
          </div>
          <div class="input">
            <label for="inputReview">Ulasan Kamu</label>
            <textarea name="inputReview" id="inputReview"></textarea>
          </div>

          <button id="reviewSubmit" type="submit">Kirim Ulasan</button>
        </form>
      </section>
    `;
  }
}

customElements.define('review-form', ReviewForm);
