class RestoDetail extends HTMLElement {
  constructor() {
    super();

    this._resto = {
      info: {},
      menus: [],
      reviews: [],
    };
  }

  setRestoDetail(value) {
    this._resto.info = {
      id: value.id,
      name: value.name,
      description: value.description,
      pictureId: value.pictureId,
      city: value.city,
      address: value.address,
      rating: value.rating,
      categories: value.categories,
    };
    this._resto.menus = value.menus;
    this._resto.reviews = value.customerReviews;
    this.render();
  }

  connectedCallback() {
    this.render();
  }

  render() {
    const container = document.createElement('div');
    container.setAttribute('class', 'resto_detail');

    const detailInfo = document.createElement('resto-detail-info');
    detailInfo.setDetailInfo(this._resto.info);
    this.innerHTML = '';

    const detailMenus = document.createElement('resto-detail-menu');
    detailMenus.setMenus(this._resto.menus);

    const detailReviews = document.createElement('resto-detail-review');
    detailReviews.setReviews(this._resto.reviews);

    const buttonLikeContainer = document.createElement('div');
    buttonLikeContainer.setAttribute('id', 'likeButtonContainer');

    const formReview = document.createElement('review-form');
    container.append(
      detailInfo,
      detailMenus,
      detailReviews,
      buttonLikeContainer,
      formReview
    );
    this.append(container);
  }
}

customElements.define('resto-detail', RestoDetail);
