import CONFIG from '../../globals/config';

class RestoDetailInfo extends HTMLElement {
  constructor() {
    super();
    this._restoInfo = {
      id: 'SAMPLE_ID',
      name: 'SAMPLE_NAME',
      description: 'SAMPLE_DESCRIPTION',
      pictureId: '',
      city: 'SAMPLE_CITY',
      address: 'SAMPLE_ADDRESS',
      rating: 'SAMPLE_RATING',
      categories: 'SAMPLE_CATEGORIES',
    };
  }

  setDetailInfo(value) {
    this._restoInfo['id'] = value['id'];
    this._restoInfo['name'] = value['name'];
    this._restoInfo['description'] = value['description'];
    this._restoInfo['pictureId'] = value['pictureId'];
    this._restoInfo['city'] = value['city'];
    this._restoInfo['address'] = value['address'];
    this._restoInfo['rating'] = value['rating'];
    this._restoInfo['categories'] = this._convertObjectToString(
      value['categories']
    );
    this.render();
  }

  connectedCallback() {
    this.render();
  }

  _convertObjectToString(items) {
    return items?.map(({ name }) => name).join(', ');
  }

  render() {
    this.innerHTML = `
      <div class="resto_detail_info">
        <div class="info_header">
          <h1 tabindex="0" class="header_name">${this._restoInfo.name}</h1>
          <p tabindex="0">${this._restoInfo.city}</p>
        </div>

        <div class="info_img">
          <picture>
            <source media="(max-width: 600px)" data-srcset="${CONFIG.BASE_IMAGE_URL_SMALL}${this._restoInfo.pictureId}">
            <source media="(max-width: 1000px)" data-srcset="${CONFIG.BASE_IMAGE_URL_MEDIUM}${this._restoInfo.pictureId}">
            <img data-src="${CONFIG.BASE_IMAGE_URL_LARGE}${this._restoInfo.pictureId}" alt="${this._restoInfo.name}" class="lazyload" loading="lazy">
          </picture>
        </div>

        <div class="info_detail">
          <h2 tabindex="0">Informasi</h2>
          <p class="info_detail_label" tabindex="0">Rating</p>
          <p tabindex="0">⭐ ${this._restoInfo.rating}</p>
          <p class="info_detail_label" tabindex="0">Kategori Restoran</p>
          <p tabindex="0">${this._restoInfo.categories}</p>
          <p class="info_detail_label" tabindex="0">Alamat</p>
          <p tabindex="0">${this._restoInfo.address}</p>
        </div>
      </div>

      <div class="detail_desc">
        <h2 tabindex="0">Deskripsi Restoran</h2>
        <p tabindex="0" class="resto_desc">${this._restoInfo.description}</p>
      </div>
    `;
  }
}

customElements.define('resto-detail-info', RestoDetailInfo);
